import { Component, OnInit } from '@angular/core';
import {ReportService} from "../../../../business/service/report.service";
import Chart from 'chart.js';

@Component({
  selector: 'app-subject-report',
  templateUrl: './subject-report.component.html',
  styleUrls: ['./subject-report.component.css']
})
export class SubjectReportComponent implements OnInit {
	
  
  
  userRole: any[];
  public canvas : any;
  public ctx;

  rows: any[];
  constructor(private reportService: ReportService) { }

  ngOnInit(): void {
    this.getSubjectReport();
  }

  getSubjectReport()
  {
    this.reportService.getSubjectReport().subscribe( resultsData => {
     this.rows = resultsData;
	  //this.total$ = resultsData.length
    //console.log(resultsData.category);
	var values =[]
	var valuesTwo =[]
    var labels = []
	var borderColor = []
	var backgroundColor= []
	var backgroundColorTwo= []
	
	resultsData.forEach(element => {
		 console.log(element.category);
		 labels.push(element.category);
		 values.push(parseInt(""+element.open));
		 valuesTwo.push(parseInt(""+element.closed));
		 backgroundColor.push('rgba(255, 99, 132, 1)')
		 backgroundColorTwo.push('rgba(75, 192, 192, 1)')
		 borderColor.push('rgba(255, 99, 132, 0.2)')
	});
	  
	  
	  
	  
	this.canvas = document.getElementById("chartBig1");
    this.ctx = this.canvas.getContext("2d");
	  var myChart = new Chart(this.ctx, {
			type: 'bar',
			data: {
				labels: labels,
				datasets: [{
					label: '# of Open Tickets',
					data: values,
					backgroundColor: backgroundColor,
					borderColor: borderColor ,
					borderWidth: 1
				},{
					label: '# of Closed Tickets',
					data: valuesTwo,
					backgroundColor: backgroundColorTwo,
					borderColor: borderColor ,
					borderWidth: 1
				}
				]
			},
			options: {
				scales: {
					yAxes: [{
						stacked:true,
						ticks: {
							beginAtZero: true
						}
					}],
					xAxes: [{
						stacked:true
						
					}]
				}
			}
		});
	  
    });
  }

}
