import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';
import {map} from 'rxjs/operators';
import {Global} from '../../Global';

@Injectable({
  providedIn: 'root'
})
export class ReportService {
  private baseUrl = this.global.baseUrl + '/api/report/';
  private getSubjectReportUrl = this.baseUrl + 'get-subject-report';
  private getCategoryReportUrl = this.baseUrl + 'get-category-report';
  constructor(private http: HttpClient, private global: Global) { }

  getSubjectReport(): Observable<any> {
    return this.http.get<any>(this.getSubjectReportUrl);
  }

  getCategoryReport(): Observable<any> {
    return this.http.get<any>(this.getCategoryReportUrl);
  }

}
