import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {DashboardComponent} from '../admin/dashboard/dashboard.component';
import {ReportsComponent} from './reports/reports.component';
import {CategoryReportComponent} from "./category-report/category-report.component";
import {SubjectReportComponent} from "./subject-report/subject-report.component";


const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Reports'
    },
    children: [
      {
        path: '',
        redirectTo: 'reports'
      },
      {
        path: 'reports',
        component: ReportsComponent,
        data: {
          title: 'Reports'
        }
      },
      {
        path: 'category-report',
        component: CategoryReportComponent,
        data: {
          title: 'Category Report'
        }
      },
      {
        path: 'subject-report',
        component: SubjectReportComponent,
        data: {
          title: 'Subject Report'
        }
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ReportsRoutingModule { }
